import React, { useEffect, useState } from "react"


import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

import Axios from "../../axios"
import { BASE_URL } from "../../config"
import Loadable from "@loadable/component"
import "owl.carousel/dist/assets/owl.carousel.css"
import "owl.carousel/dist/assets/owl.theme.default.css"
import { doc } from "prettier"
import Slider from "react-slick";

const query = graphql`
    {
      allStrapiServices {
        nodes {
          img {
            localFile {
              childImageSharp {
                original {
                  src
                }
              }
            }
          }
          name
          service_category {
            name
            slug
          }
          services {
            services
          }
        }
      }

    }
`


const OwlCarousel = Loadable(() => import("react-owl-carousel"))
export default function SimpleSlider() {
  var settings = {
    dots: false,
    autoplay: true,
    infinite: true,
    speed: 1000,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  //   responsiveClass: true,
  //   responsive: {
  //     0: {
  //         items: 1,
  //     },
  //     400: {
  //         items: 1,
  //     },
  //     600: {
  //         items: 2,
  //     },
  //     700: {
  //         items: 3,
  //     },
  //     1000: {
  //         items: 5,
  //     }
  // },
  };

  const data = useStaticQuery(query)
  const services = data.allStrapiServices.nodes

  // const [Services, setServices] = useState([])

  // useEffect(() => {
  //   async function fetchData() {
  //     const request = await Axios.get("/services")
  //     setServices(request.data)
  //     return request
  //   }
  //   fetchData()
  // }, [])
  return (
    <section id="services">
         <section className="container-fluid">
            <section className="row">
             <section className="container">
               <section className="row">
                 <section className="col-md-12 mx-md-0">
                  
                   <h1 className="h-services mt-2">Services</h1>
                   <div class="line-service"></div>
                    {/* <div className="hr">
                      <hr className="hr-msar hr-map-service" style={{width:`${7*24}px`}} />
                     </div> */}

    <Slider {...settings}>
    {services?.map((document, i) => (
    <div class="cell">
                         <img
                                src={document.img.localFile.childImageSharp?.original.src}
                                className="img-fluid"
                              />
                         <article class="red">
                           <h5 style={{marginTop:"20px",textAlign: "center",}} >{document?.name}</h5>
                           <div class="hideProductTitle">
                           {document.services.services.map(item => (
                             <p class="slide-p">{item}</p>                        
                           ))}</div>
                          <div class="stage">
                          <Link class="stage-a"
                                  to={`/service/${document?.service_category?.slug}`}
                                >
                                  Read more
                                </Link>
                            
                           </div>
                         </article>
                       </div>
  ))}
    </Slider>
    </section>
              </section>
            </section>
          </section>
        </section>
      </section>
  
  );
}
