import React, { useState, useEffect } from "react"
import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import PropTypes from "prop-types"
import Axios from "../../axios"
import { BASE_URL } from "../../config"


const query = graphql`
    {
      allStrapiNewes {
        nodes {
          img {
            localFile {
              childImageSharp {
                original {
                  src
                }
              }
            }
          }
        }
      }

    }
`


function News(props) {

  const data = useStaticQuery(query)
  const news = data.allStrapiNewes.nodes


  // const [News, setNews] = useState([])
  // useEffect(() => {
  //   async function fetchData() {
  //     const request = await Axios.get("/newes")
  //     setNews(request.data)
  //     return request
  //   }
  //   fetchData()
  // }, [])

  const [imageIndexes, setImageIndexes] = useState([0,1,2,3,4,5])
  const imageMover = (index) => {
    let temp = imageIndexes;
    // let spare = temp[0]
    temp[0] = index
    // temp[index] = spare
    setImageIndexes([...temp])
  }

  return (
    <section id="news" className>
      <section className="container-fluid news bg-msar-dark bg-news-service">
        <section className="row">
          <section className="container ">
            <section className="row">
              <section className="col-md-12 mt-2">
                {/* <button className="btn btn-msar btn-sm text-uppercase mt-5">
                  We are eastern Msar{" "}
                </button> */}
                <h1 className="h-news my-1">Gallery</h1>
                <div class="line-white"></div>
                {/* <div className="hr">
                  <hr className="hr-msar hr-msar-news" style={{width:`${7*20}px`}} />
                </div> */}
                <div className="row mt-4">
                  <div className="col-md-8">
                    <div
                      className="news-box"
                      // onClick={() => navigate(`/events/${News[0]?.id}`)}
                    >
                      <div className="news-image"
                        onClick={() => imageMover(imageIndexes[0])}
                      >
                        <img
                          // src={`${BASE_URL}${News[imageIndexes[0]]?.img?.url}`}
                          src={news[imageIndexes[0]].img.localFile.childImageSharp?.original.src}
                          className="img-fluid img-news-md"
                        />
                      </div>
                      <div className="news-details">
                        {/* <div className="news-details-date">27-Nov-2020</div>
                        <div className="news-details-text text-white">
                          {News[0]?.title}
                        </div> */}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-4 pl-md-0 mt-3 mt-md-0">
                    <div className="row">
                      <div className="col-md-12 mb-3">
                        <div className="news-box">
                          <div className="news-image"
                              onClick={() => imageMover(imageIndexes[1])}
                          >
                            <img
                              // src={`${BASE_URL}${News[imageIndexes[1]]?.img?.url}`}
                              src={news[imageIndexes[1]].img.localFile.childImageSharp?.original.src}
                              className="img-fluid img-news-sm"
                            />
                          </div>
                          <div className="news-details">
                            {/* <div className="news-details-date">27-Nov-2020</div>
                            <div className="news-details-text text-white">
                              {News[1]?.title}
                            </div> */}
                          </div>
                        </div>
                      </div>
                      <div className="col-md-12">
                        <div className="news-box">
                          <div className="news-image"
                            onClick={() => imageMover(imageIndexes[2])}
                          >
                            <img
                              // src={`${BASE_URL}${News[imageIndexes[2]]?.img?.url}`}
                              src={news[imageIndexes[2]].img.localFile.childImageSharp?.original.src}
                              className="img-fluid img-news-sm"
                            />
                          </div>
                          <div className="news-details">
                            {/* <div className="news-details-date">27-Nov-2020</div>
                            <div className="news-details-text text-white">
                              {News[2]?.title}
                            </div> */}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row mt-3">
                  <div className="col-md-4 mb-3 mb-md-0 pr-3">
                    <div className="news-box">
                      <div className="news-image"
                        onClick={() => imageMover(imageIndexes[3])}
                      >
                        <img
                          // src={`${BASE_URL}${News[imageIndexes[3]]?.img?.url}`}
                          src={news[imageIndexes[3]].img.localFile.childImageSharp?.original.src}
                          className="img-fluid img-news-sm"
                        />
                      </div>
                      <div className="news-details">
                        {/* <div className="news-details-date">27-Nov-2020</div>
                        <div className="news-details-text text-white">
                          {News[3]?.title}
                        </div> */}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-4 mb-3 mb-md-0 pl-md-0">
                    <div className="news-box">
                      <div className="news-image"
                        onClick={() => imageMover(imageIndexes[4])}
                      >
                        <img
                          // src={`${BASE_URL}${News[imageIndexes[4]]?.img?.url}`}
                          src={news[imageIndexes[4]].img.localFile.childImageSharp?.original.src}
                          className="img-fluid img-news-sm"
                        />
                      </div>
                      <div className="news-details">
                        {/* <div className="news-details-date">27-Nov-2020</div>
                        <div className="news-details-text text-white">
                          {News[4]?.title}
                        </div> */}
                      </div>
                    </div>
                  </div>
                  <div className="col-md-4 mb-3 mb-md-0 pl-md-0">
                    <div className="news-box">
                      <div className="news-image"
                        onClick={() => imageMover(imageIndexes[1])}
                      >
                        <img
                          // src={`${BASE_URL}${News[imageIndexes[1]]?.img?.url}`}
                          src={news[imageIndexes[1]].img.localFile.childImageSharp?.original.src}
                          className="img-fluid img-news-sm"
                        />
                      </div>
                      <div className="news-details">
                        {/* <div className="news-details-date">27-Nov-2020</div>
                        <div className="news-details-text text-white">
                          Lorem ipsum dolor sit amet.
                        </div> */}
                      </div>
                    </div>
                  </div>
                </div>
              </section>
            </section>
          </section>
        </section>
      </section>
    </section>
  )
}

News.propTypes = {}

export default News
